:root {
  --aqua-marin: #54e6af;
  --dark-blue-grey: #2c344b;
  --mirage: #121725;
  --white: #ffffff;
  --marble-blue: #5a668a;
  --light-blue-grey: #c2cbe5;
}

.text-aqua-marin {
  color: var(--aqua-marin);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Chivo", sans-serif;
  font-weight: 300;
  background: var(--mirage);
}

.title {
  font-size: 3.25rem;
  font-weight: 300;
  line-height: 3.875rem;
  text-transform: uppercase;
  color: var(--white);
}

body {
  font-size: 1.125rem;
  font-weight: 300;
  line-height: 1.75rem;
  color: var(--light-blue-grey);
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.button {
  font-family: inherit;
  font-size: rem(14);
  font-weight: 700;
  color: var(--mirage);
  padding: rem(9) rem(27);
  background: var(--aqua-marin);
}

.email-input {
  width: 15.625rem;
  height: 3.5rem;
  padding: 0.875rem 2rem;
  background: var(--dark-blue-grey);
  color: var(--white);
}
.email-input::placeholder {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--light-blue-grey);
}